body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.ant-input.ant-input-lg {
    width: 100%;
    max-width: 720px;
    text-align: center; 
    border-radius: 36px;
    height: 40px;
}

.search
 {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;

  width: 100%;
  max-width: 720px;

  padding-top: 5%;
  padding-bottom: 5%;
  margin-left: auto;
  margin-right: auto;

}

.header{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.header h1{
  -webkit-align-self: center;
          align-self: center;
  color: white;
  font-weight: bold;
}

.anticon-thunderbolt{
    color: #FFEB3B;
    font-size: 4rem;
}


@media (max-width: 768px) {
  .header {
    margin-bottom: 0.5rem;
  }
  .header h1{
    font-size: 1.2rem;
  }
  .anticon-thunderbolt{
    font-size: 2.4rem;
  }
  .ant-input.ant-input-lg {
    font-size: 0.8rem;
    height: 1.6rem;
  }
}
@media (min-width: 768px) {
  .header {
    margin-bottom: 1rem;
  }
  .header h1{
    font-size: 1.6rem;
  }
  .anticon-thunderbolt{
    font-size: 3.2rem;
  }
  .ant-input.ant-input-lg {
    font-size: 1rem;
    height: 2rem;
  }
}
@media (min-width: 992px) {
  .header {
    margin-bottom: 1rem;
  }
  .header h1{
    font-size: 1.9rem;
  }
  .anticon-thunderbolt{
    font-size: 3.8rem;
  }
  .ant-input.ant-input-lg {
    font-size: 1.2rem;
    height: 2.4rem;
  }
}

.taken > a > .ant-card > .ant-card-body > .ant-card-meta > .ant-card-meta-detail > .ant-card-meta-title{
    text-decoration: line-through;
    color: rgba(0, 0, 0, 0.45);
}

.taken > a > .ant-card-hoverable:hover{
    box-shadow: 0 0px 0px rgba(0, 0, 0, 0.09);
}

.available > a > .ant-card{
    border-color: #FFEB3B;
    border-width: 0.1rem;
    background-color: rgba(255,235,59, 0.05);
}

.available > a > .ant-card-hoverable:hover{
    box-shadow: 4px 6px 8px rgba(0, 0, 0, 0.09);
    border-color: #FFEB3B;
    background-color: rgba(255,235,59, 0.15);
}



.card {
    margin: 1%;
}

.card > .ant-card{
    border-radius: 0.4rem;
    max-height: 6rem;
}

.card > a > .ant-card{
    border-radius: 0.4rem;
    max-height: 6rem;
}

@media (max-width: 768px) {
    .card {
      width: 48%;
    }
}
@media (min-width: 768px) {
    .card {
      width: 31%;
    }
}
@media (min-width: 992px) {
    .card {
      width: 23%;
    }
}
@media (min-width: 1200px) {
    .card {
      width: 18%;
    }
}
.results{
  padding-top: 2rem;
  padding-bottom: 2rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.pull-right{
    float: right;
}

.pull-left{
    float: left;
}

ul {
    list-style-type: none;
    display: -webkit-flex;
    display: flex;
    padding: 0;
    margin: 0;
}

li:not(:last-child) {
    margin-right: 1rem;
}

a{
    background:none;
    color:inherit !important;
    border:none;
    font: inherit;
    transition: 0s !important;
}

a:hover{
    color: black !important;
}

@media (max-width: 768px) {
    .footer {
        padding-top: 0.5rem;
        padding-bottom: 2.5rem;
    }
}
@media (min-width: 768px) {
    .footer {
        padding-top: 1rem;
        padding-bottom: 3rem;
    }
}
@media (min-width: 992px) {
    .footer {
        padding-top: 1rem;
        padding-bottom: 3.5rem;
    }
}
@media (min-width: 1200px) {
    .footer {
        padding-top: 1.5rem;
        padding-bottom: 4rem;
    }
}
.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

.jumbotron{
  background-image: url(/instant-username-search/static/media/stars.6494ab5b.png);
  background-color: black;
  background-attachment: fixed;
}

hr {
  border: 0;
  height: 0;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}

.landing{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 5%;
  margin-bottom: 5%;
}

.landing .astronaut{
  width: 7rem;
  height: 7rem;
  margin-top: auto;
  margin-bottom: auto;
}

.landing .intro h2{
  font-weight: bold;
}

@media (max-width: 768px) {
  .landing .intro h2{
    font-size: 1rem;
  }

  .landing .astronaut{
    width: 5rem;
    height: 5rem;
  }
}
@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}
@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
  .landing .intro p{
    font-size: 1rem;
  }
}
